import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import DecentralizedExchangeScreen from "../screens/decentralized-exchange-dev";

const DecentralizeExchangePage = () => {
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Decentralized Exchange Development", link: "" },
  ];
  return (
    <Layout>
      <Seo title="Decentralized Exchange Development" />
      <BreadCrumb items={breadcrumbLinks} />
      <DecentralizedExchangeScreen />
    </Layout>
  );
};

export default DecentralizeExchangePage;
