import React from "react";
import { Card } from "react-bootstrap";
import { Swiper, SwiperSlide } from "swiper/react";
import {
  Navigation,
  Scrollbar,
  A11y,
  EffectFade,
  Autoplay,
} from "swiper/modules";
import { BreakPoints } from "../../core/const";
const Carousel = () => {
  const items = [
    {
      title: "Automated Market Maker (AMM)",
      description:
        "An automated market maker (AMM) harnesses the power of smart contracts to facilitate orders and monitor the prices of digital assets. Instead of playing the role of matchmaker between buyers and sellers, AMMs rely on liquidity pools funded by the community to carry out transactions. Whenever someone engages in token trading within a liquidity pool, it results in a shift in the balance of tokens within that pool, subsequently influencing the price of the tokens it contains. With a multitude of AMMs operating with their own liquidity pools, the price of a specific asset can vary from one exchange to another. Professional traders often capitalize on these disparities by purchasing assets from exchanges with lower prices and then selling them into pools where prices are higher.",
    },
    {
      title: "DEX Aggregators",
      description:
        "DEX aggregators are often considered decentralized exchange search engines. They serve as a central hub that consolidates information from various decentralized exchanges, sparing users the hassle of manually hunting for the most favorable token prices. These platforms leverage a variety of protocols to gather liquidity from multiple decentralized exchanges, a strategy that allows them to reduce slippage on substantial orders, decrease trading fees, and present users with the best possible token prices.",
    },
    {
      title: "Order book (off-chain and on-chain)",
      description:
        "Order books in decentralized exchanges bear a resemblance to those in centralized exchanges. They empower traders to input buy orders at their desired purchase price for a particular digital asset, as well as sell orders at the price they deem fit for selling the asset. In on-chain order book DEXs, every order and transaction is meticulously documented on the blockchain, resulting in heightened transparency and decentralization as each transaction is openly visible to all. Off-chain order book DEXs streamline on-chain trading activities by handling trade matching and retaining trade-related data off the blockchain. In this setup, only the transaction for the ultimate trading settlement is executed on-chain. This approach significantly enhances transaction speeds and lowers gas fees compared to on-chain DEXs.",
    },
  ];

  return (
    <Swiper
      modules={[Navigation, Scrollbar, A11y, EffectFade, Autoplay]}
      slidesPerView={2}
      spaceBetween={30}
      loop={false}
      autoplay={false}
      breakpoints={BreakPoints}
      className="decenter_exc_dev_slider"
    >
      {items.map((ele, ind) => {
        const { title, description } = ele;
        return (
          <SwiperSlide key={ind} className="py-2">
            <Card className="decenter_exc_swiper">
              <Card.Body>
                <div className="decenter_exc_card_body">
                  <h3>{title}</h3>
                  <p>{description}</p>
                </div>
              </Card.Body>
            </Card>
          </SwiperSlide>
        );
      })}
    </Swiper>
  );
};

export default Carousel;
